.product {
	display: -ms-flexbox;
	display: flex;
	text-align: left;
	margin: 20px;
	padding: 10px;
	border-bottom: 1px solid lightgrey;
}

.product-name {
	color: blue;
	font-weight: 400
}

.product-info {
	display: -ms-flexbox;
	display: flex;
	/*flex-direction: column;*/
}

.product-info > div {
	width: 50%;
}

button {
	width: 150px;
	height: 30px;
	background-color: goldenrod;
	border-radius: 5px;
	border: 1px solid black;
	-webkit-box-shadow: 0 2px #666;
	        box-shadow: 0 2px #666;
}

button:hover {
	cursor: pointer;
	opacity: 0.9;
}

button:active {
	-webkit-transform: translateY(2px);
	    -ms-transform: translateY(2px);
	        transform: translateY(2px);
}


